.container {
  max-width: 1350px;
  @include media-query('large') {
    padding-left: 18px;
    padding-right: 18px;
  }
  .row {
    @include media-query('large') {
      margin-left: -18px;
      margin-right: -18px;
    }
  }
  .col-12 {
    @include media-query('large') {
      padding-left: 18px;
      padding-right: 18px;
    }
  }
}
.container-lg {
  max-width: 1350px;
}

.container-fluid {
  @include media-query('large') {
    padding-left: 18px;
    padding-right: 18px;
  }
  .row {
    @include media-query('large') {
      margin-left: -18px;
      margin-right: -18px;
    }
  }
  .col-12 {
    @include media-query('large') {
      padding-left: 18px;
      padding-right: 18px;
    }
  }
}

img {
  max-width: 100%;
}

.space {
  padding: 80px 0;

  @include media-query('mobile') {
    padding: 50px 0;
  }

}
.space-top {
  padding-top: 80px;
  @include media-query('mobile') {
    padding-top: 50px;
  }
}
.space-bottom {
  padding-bottom: 80px;
  @include media-query('mobile') {
    padding-bottom: 50px;
  }
}

.bg-img {
  background-position: center !important;
  background-size: cover !important;
}

.instagram-posts {
  iframe {
    width: 100%;
    border-radius: 10px !important;
    height: 800px;

    @include media-query('medium') {
      height: 650px;
    }
  }
}

body {
  .space-m-top-none{
    margin-top: 0;
  }
  .space-p-top-none{
    padding-top: 0;
  }
  .space-m-bottom-none{
    margin-bottom: 0;
  }
  .space-p-bottom-none{
    padding-bottom: 0;
  }
}
.node__content{
  p {
    font-size: 18px;
  }
}

.space-m{
  margin: 80px 0;
  @include media-query('mobile') {
    margin: 50px 0;
  }
}
.space-mt{
  margin-top: 80px;
  @include media-query('mobile') {
    margin-top: 50px;
  }
}
.space-mb{
  margin-bottom: 80px;
  @include media-query('mobile') {
    margin-bottom: 50px;
  }
}

.user-logged-in nav.tabs {
  position: relative;
  z-index: 11;
  margin: 0;
}

.user-logged-in nav.tabs ul {
  padding: 20px;
}

.heading-with-top-border h2 {
  margin-top: 80px;
  padding-top: 40px;
  border-top: 1px solid #000;
  @include media-query ('small') {
    margin-top: 35px;
    padding-top: 25px;
  }
}

.moderation-alert p {
  background: #EBF2FF;
  display: inline-flex;
  padding: 20px 18px 14px 18px;
  border-radius: 10px;
  font-size: 18px;
  font-family: $teritoryFont;
  gap: 10px;
  align-items: center;
  width: 100%;
  color: $primaryColor;
  font-weight: 600;
  margin-bottom: 40px;
  @media(max-width: 767px) {
    font-weight: 400;
    padding: 15px;
    font-size: 12px;
  }
  svg {
    position: relative;
    top: -3px;
  }
}

.user-roles {
  pre {
    margin: 0;
    code {
      font-family: $secondaryFont;
      text-transform: capitalize;
      font-size: 60px;
      line-height: 60px;
      font-weight: 700;
      margin: 60px 0 0 0;
      display: block;
      border-top: 1px solid $primaryColor;
      padding-top: 40px;
      @media(max-width: 1300px) {
        font-size: 25px;
        line-height: 38px;
      }
    }
  }
}

.request-box {
  padding: 30px;
  border-radius: 10px;
  max-width: 875px;
  margin-top: 30px;
  margin-bottom: 30px;
  &.blue{
    background: #EBF2FF;
  }
  &.green{
    background: #B9F1C3;
  }
  h4 {
    margin-bottom: 30px;
    @include media-query ('mobile') {
      text-align: center;
    }
  }

  h6 {
    font-size: 18px;
    font-weight: 700;
    font-family: $teritoryFont;
    position: relative;
    padding-left: 45px;
    @include media-query ('mobile') {
      padding-left: 0px;
      text-align: center;
      padding-top: 40px;
    }
    &:before {
      background: url("../../assets/images/info-icon.svg")no-repeat;
      content: "";
      position: absolute;
      left: 5px;
      top: -5px;
      width: 26px;
      height: 26px;
      @include media-query ('mobile') { 
        left: 50%;
        top: 0;
        margin-left: -13px;
      }
    }
    a {
      text-decoration: underline;
      text-transform: uppercase;
    }
  }
  ul {
    list-style-type: none;
    margin: 30px 0;
    padding: 0 0 0 45px;
    font-family: $teritoryFont;
    @include media-query ('mobile') {
      padding: 0;
      font-size: 14px;
    }
    strong {
      font-weight: 600;    
      width: 110px;
      @include media-query ('mobile') {
        width: 95px;
      }
    }
    span {
      display: block;
      width : calc(100% - 130px);
      @include media-query ('mobile') {
        width : calc(100% - 105px);
      }
    }
    li {
      display: flex;
      column-gap: 20px;
      margin-bottom: 10px;
      @include media-query ('mobile') {
        column-gap: 10px;
      }
    }
  }

  .footer-note {
    padding-left: 45px;
    padding-top: 30px;
    border-top: 1px solid $primaryColor;
    @include media-query ('mobile') {
      padding-left: 0px;
      padding-top: 20px;
    }
    h6 {
      font-weight: 700;
      font-family: $teritoryFont;
      padding-left: 0;
      @include media-query ('mobile') {
        text-align: left;
        padding-top: 0;
      }
      &:before {
        display: none;
      }
    }
    p {
      font-family: $teritoryFont;
    }
  }
}